                            <div className="d-none" id="projectJson92" data-project-json="{&quot;id&quot;:92,&quot;name&quot;:&quot;Orbitau&quot;,&quot;state&quot;:&quot;applications_open&quot;,&quot;ido_starts_at&quot;:&quot;2022-02-23T13:00:00.000Z&quot;,&quot;min_allocation&quot;:null,&quot;max_allocation&quot;:null,&quot;total_raise&quot;:245000.0,&quot;logo_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/1pxylpsiokyszbbfy3eowrmynqz3&quot;,&quot;card_background_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/fhozkvzcelx0g9hz4dgug7dh16b3&quot;,&quot;cover_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/me66enzawy9fkmxow5f9cg26r9s5&quot;,&quot;website_url&quot;:&quot;https://orbitau.io/&quot;,&quot;twitter_handle&quot;:&quot;@Orbitauofficial&quot;,&quot;telegram_handle&quot;:&quot;OrbitauGlobal&quot;,&quot;twitter_url&quot;:&quot;https://twitter.com/@Orbitauofficial&quot;,&quot;telegram_url&quot;:&quot;https://t.me/OrbitauGlobal&quot;,&quot;medium_url&quot;:&quot;https://medium.com/@OrbitauOfficial&quot;,&quot;linkedin_url&quot;:&quot;&quot;,&quot;discord_url&quot;:&quot;https://dsc.gg/orbitau?__cf_chl_tk=Gp0f_vRI0yyS2z0.sC4QZUZeGxBWk_lv9csiNNCPNGA-1644353695-0-gaNycGzNCL0&quot;,&quot;github_url&quot;:&quot;&quot;,&quot;founded_at&quot;:&quot;2022-02-08&quot;,&quot;whitepaper_url&quot;:&quot;&quot;,&quot;description&quot;:&quot;## Highlights \r\n*  Orbitau is a turn-based idle combat/socializing NFT game, inspired by Norse Mythology and the Seven Deadly Sins.\r\n*  Characters in Orbitau can be customized by users with countless\r\nchoices from appearance to stats, creating a colourful world. \r\n*   Orbitau had raised $3 million USD in funding in just 30 days from a number of well-known investors: NGC, LD Capital, Au21 Capital, X21 Digital.\r\n\r\n## Product \r\n### Orbitau, a fantasy game inspired by Norse Mythology and the Seven Deadly Sins\r\n\r\nAfter The Ragnarok, the spacetime between universes became distorted and twisted. The souls of the gods who died in battle could not return to Heaven but instead fell to Hell, where the Seven Deadly Sins awaited them. The Seven Princes of Hell, including Lucifer, Beelzebub, Leviathan, Asmodeus, Behemoth, Mammon, and Belphegor, share control of seven distinct territories, all subject to Lucifer's supreme pride. Masses of demonic armies cracked down on the gods, who were struggling in a place where they never meant to be.\r\n\r\nThey eventually formed an alliance and summoned fallen souls and deceased heroes to defeat the Seven Deadly Sins once and for all. The battleground has been set, and the territory ruled by the Jealous Leviathan will be the alliance's first target. Glory or Dust? The appearance of you, who was summoned to this final battle, will shift the course of fate in ways that no god or demon lord could have predicted\&quot;.\r\n\r\nOrbitau, a fantasy game inspired by Norse Mythology and the Seven Deadly Sins, auspiciously provides players with new experiences based on well-known legends. In Orbitau, players will directly participate in the Demon-God war with the familiar role-playing and turn-based strategy gameplay in a magical land of mixed reality and fiction created by blockchain technology. And the rewards are materially valuable, not just symbolic victories.\r\n![Orbitau Product](https://res.cloudinary.com/polkastarter/image/upload/v1644406021/projects/orbitau/orbitau-gameplay.png)\r\n\r\n## Problem\r\n### Many NFT Game projects fail to meet the core values of a video game - to entertain and enrich player experience\r\n\r\nNFT games, which are built on a blockchain platform and allow players to hunt for high-value items in the form of NFTs, have made gaming a time-consuming activity for many people, but the only appeal is profit, not game experience. As a result, despite being based on the \&quot;gaming\&quot; foundation, many NFT Game projects fail to meet the core values of a video game - to entertain and enrich the player experience.\r\n\r\nRecognizing these shortcomings, the Orbitau development team set out to create an NFT game that not only helps players \&quot;Play-to-Earn,\&quot; but also \&quot;Play-and-Earn\&quot; while offering real immersive experiences in this world. With Orbitau, gamers can fully immerse themselves in the most exciting gaming world while earning real money through fair and exciting in-game activities. At its core, this mechanism derives from Orbitau's ability to seamlessly combine two of its strengths: storyline and game features.\r\n\r\n## Solution \r\n### A good storyline will pique players' interest\r\nOrbitau will be an NFT game that allows players to truly \&quot;Play-and-Earn\&quot; while balancing entertaining experiences with opportunities to earn real money, with a smooth combination and progression between storyline and game features.\r\n\r\n* **Storyline:** A good storyline will pique players' interest since it is impossible to predict what will happen next, coercing them to continue the journey to reach the final destination. In essence, Oribitau’s storyline is based on a combination of Norse Mythology and the Seven Deadly Sins. What happens when Thor, Odin, and Loki from Norse mythology fall into a twisted timeline leading straight to hell where the Seven Deadly Sins rule? Perhaps it is where Good does not triumph over Evil. It makes players wonder when plot twists unfold in such a way that nothing is quite righteous. In this game, the seven greatest sins of men - pride, greed, lust, gluttony, envy, wrath, and laziness - are represented by the Seven Princes of Hell, including Lucifer, Mammon, Asmodeus, Beelzebub, Leviathan, Behemoth, and Belphegor. \r\n\r\n* **Game features:** No matter how good it is, everything can easily fall apart if the storyline is not expressed through unique features that are reasonably added alongside. Stat development will play a vital role not only in combat strength but also in the storyline as the gods increasingly overwhelm hellish devils or as the player receives a level penalty when losing to a surprise ambush. The weapon and upgrade system is not pre-programmed; players will instead get them at random through rewards from main or extended side quests. Of course, the quest system will reveal concurrently with the plot, so the extras are the consequence of unexpected variables arising from plot twists.\r\n\u003cdiv class=\&quot;ratio ratio-16x9\&quot;\u003e\r\n  \u003ciframe src=\&quot;https://www.youtube.com/embed/eZknZ_Y9qSI\&quot; title=\&quot;YouTube Mecha Morphing\&quot; allowfullscreen\u003e\u003c/iframe\u003e\r\n\u003c/div\u003e\r\n\r\n\r\n## Business Model\r\n### Executing the traditional gaming business model well\r\nHow will gamers earn? To get started in the game, players can either purchase eggs or buy heroes on the marketplace with TAUM. You need to have at least one hero to start playing. By participating in the game, users can earn in various ways, such as through daily quests, story quests, PvP mode, lucky events, breeding fee, winner-take-all rewards, clan wars and staking. \r\nThere are also various interactions, in which tokens will be burnt, including the upgrading of heroes and skills, purchasing of items, pets and land, PvP games, lucky events, conversion tax, breeding costs and NFT marketplace fees.\r\nThe project encourages breeding on heroes so that players can burn ORI tokens aggressively and reduce inflation. Eggs will be sold for a limited number only. Orbitau will generate revenue through conversion tax, the NFT marketplace fee of 4.25%.\r\n\u003cdiv class=\&quot;ratio ratio-16x9\&quot;\u003e\r\n  \u003ciframe src=\&quot;https://www.youtube.com/embed/Fiz7YVJuUUw\&quot; title=\&quot;YouTube Mecha Morphing\&quot; allowfullscreen\u003e\u003c/iframe\u003e\r\n\u003c/div\u003e\r\n\r\n\r\n## Market \r\n### Competition between idle-to-earn games\r\n Game-Fi dates all the way back to November 2019, when MixMarvel's founders stated at the World Blockchain Conference that virtual currency technology has the potential to revolutionize the gaming industry. \r\n\r\nSince then, the term \&quot;Game-Fi\&quot; has become widely used to refer to games that incorporate a financial component powered by blockchain technology. What truly distinguishes Game-Fi is its potential for financial independence. Rather than simply playing games and enriching game developers, blockchain technology incentivizes players to profit from their initial investment. \r\n\r\nAccordingly, players can access the game from anywhere in the world and earn rewards and real money from their virtual assets. This enables anyone with a computer and Internet access to earn money online, as evidenced by the large number of Axie Infinity players in the Philippines. \r\n\r\nInterest in NFT has grown in recent years, and blockchains optimized for various tokens have been launched, fueling Game-Fi's incredibly rapid growth. The enormous success of Axie Infinity, Decentraland, and The Sandbox has resulted in the appearance of a series of blockchain games, confirming the future of Game-Fi in the Metaverse era with NFT as a key ingredient.\r\n\r\nHowever, there have been numerous failures in this field; the majority of current Play-to-Earn games aren’t cool: the plot is uninteresting, the gameplay is simple, and the graphics are underdeveloped. They have relied solely on the “play to earn\&quot; selling point to retain players - and this motivation will wear off quickly if the game lacks intrinsic value. Thus, while some well-rounded titles have been successful, the majority have been seasonal. Players and investors around the world are, in particular, still waiting for more mature projects.\r\n\r\nThis problem is especially more acute within the market of idle-to-earn games. So, Orbitau will have many advantages and high competitiveness.\r\n\r\n## Investors  \r\n### Raised $3 million USD in funding in just 30 days from a number of top investors\r\nWith an experienced team and a large amount of capital mobilized in a short period of time, the Orbitau project also established successful collaborative relationships with top blockchain investment firms and advisors, such as X21 Digital, AU21 Capital, NGC Ventures, and LD Capital. \r\n\r\nOrbitau also collaborates with a number of other partners and investors, including Mexc Global, Readline DAO, Lavender Capital, Coincu Ventures, DEO networks, Monster Box, Rada, and HUB Global, all of which are well-known in the digital currency community.\r\n![Orbitau Investors](https://res.cloudinary.com/polkastarter/image/upload/v1644759543/projects/orbitau/orbitau-investors.png)\r\n\r\n## Team \r\n### High-profile team with rich experience in related fields\r\nF6 Galaxy is the core team implementing the Orbitau project, and it is made up of experts with an average of more than 11 years of experience in the fields of technology and business development, with businesses worth more than $400 million USD. \r\n\r\nThey have worked for companies such as Paypal, Google, Havas, CMN Online, Microsoft, BeGroup, Agribank, and others, and they believe that blockchain technology and the DeFi/GameFi market have a lot of untapped potential to make the world a more comfortable and secure place for everyone.\r\n\r\nThe team is led by Matt Minh T Nguyen, Chief Technology Officer Co-founder and ex-CTO of beGroup, the second-largest ride-hailing/delivery platform in Vietnam, at a current valuation of $300M USD. He’s the former CTO of ILA Vietnam, developed one of the largest EdTech platforms in Vietnam, serving 2M users. Matt was also an engineering director/architect at PayPal, Risk and Compliance. He has 15 years of experience in building Fintech and eCommerce platforms. Matt is advised by Thi Truong - Founder \u0026 CEO of Icetea Labs, GameFi and Redkite. Mr Deeban, the CEO of the GD10 Ventures, is also an advisor. \r\n![Orbitau Team](https://res.cloudinary.com/polkastarter/image/upload/v1644353520/projects/orbitau/orbitau-team.png)\r\n\r\n## Token Utility \r\n### $TAUM is the governance token for all games in Orbitau’s ecosystem\r\n$TAUM is utilized throughout the Orbitau ecosystem and NFT marketplace to purchase products and services. $TAUM holders will have voting rights in Taureum-related projects. In Orbitau, users can use $TAUM to purchase items, eggs and heroes on the NFT marketplace, to breed eggs and to upgrade major heroes.\r\n![Orbitau Tokenomics](https://res.cloudinary.com/polkastarter/image/upload/v1644405980/projects/orbitau/orbitau-token.png)&quot;,&quot;legal_name&quot;:&quot;&quot;,&quot;ethereum&quot;:false,&quot;bsc&quot;:true,&quot;polygon&quot;:false,&quot;celo&quot;:false,&quot;application_form_url&quot;:null,&quot;kyc_form_url&quot;:&quot;&quot;,&quot;published_at&quot;:&quot;2022-02-10T14:09:27.437Z&quot;,&quot;applications_opened_at&quot;:&quot;2022-02-11T14:52:38.237Z&quot;,&quot;applications_closed_at&quot;:null,&quot;kyc_opened_at&quot;:null,&quot;kyc_closed_at&quot;:null,&quot;submitted_to_kyc_at&quot;:null,&quot;submitted_to_lottery_at&quot;:null,&quot;contracts_deployed_at&quot;:null,&quot;created_at&quot;:&quot;2022-02-08T20:55:17.116Z&quot;,&quot;updated_at&quot;:&quot;2022-02-14T15:51:06.912Z&quot;,&quot;permitted_events&quot;:[&quot;close_applications&quot;],&quot;permitted_states&quot;:[&quot;applications_closed&quot;],&quot;slug&quot;:&quot;orbitau&quot;,&quot;short_description&quot;:&quot; The Orbit of Treasures&quot;,&quot;ido_ends_at&quot;:&quot;2022-02-23T15:00:00.000Z&quot;,&quot;ido_distributed_at&quot;:&quot;2022-02-23T15:00:00.000Z&quot;,&quot;prospect_usd_token_price&quot;:0.035,&quot;prospect_initial_market_cap&quot;:null,&quot;prospect_initial_token_circulation&quot;:null,&quot;token_symbol&quot;:&quot;TAUM&quot;,&quot;ido_readiness_state&quot;:&quot;applications_open&quot;,&quot;pool_contracts_available&quot;:false,&quot;pool_contract_address&quot;:&quot;&quot;,&quot;pool_chain&quot;:&quot;&quot;,&quot;finalized&quot;:false,&quot;finalized_today&quot;:false,&quot;applications_open_at&quot;:null,&quot;applications_close_at&quot;:&quot;2022-02-19T13:00:00.000Z&quot;,&quot;pool_slug&quot;:&quot;&quot;,&quot;pool_progress&quot;:0,&quot;main_pool_id&quot;:null,&quot;pool_token_to&quot;:&quot;&quot;,&quot;pool_token_address&quot;:&quot;&quot;,&quot;pool_is_live&quot;:false,&quot;pool_is_upcoming&quot;:false,&quot;hidden&quot;:false}" />
