                            <div className="d-none" id="projectJson58" data-project-json="{&quot;id&quot;:58,&quot;name&quot;:&quot;Mecha Morphing&quot;,&quot;state&quot;:&quot;applications_open&quot;,&quot;ido_starts_at&quot;:&quot;2022-02-28T13:00:00.000Z&quot;,&quot;min_allocation&quot;:null,&quot;max_allocation&quot;:250.0,&quot;total_raise&quot;:400000.0,&quot;logo_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/hlnf988mu9e12xf89ros1fgi7xaf&quot;,&quot;card_background_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/lrzvu3tirxvrm9gj7t83xnv7pws2&quot;,&quot;cover_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/5r6eton7yum7k05qyt2c8kuf0zn6&quot;,&quot;website_url&quot;:&quot;https://mechamorphing.com/&quot;,&quot;twitter_handle&quot;:&quot;Mecha_Morphing&quot;,&quot;telegram_handle&quot;:&quot;Mecha_Morphing&quot;,&quot;twitter_url&quot;:&quot;https://twitter.com/Mecha_Morphing&quot;,&quot;telegram_url&quot;:&quot;https://t.me/Mecha_Morphing&quot;,&quot;medium_url&quot;:&quot;https://medium.com/@mechamorphinggaming&quot;,&quot;linkedin_url&quot;:&quot;&quot;,&quot;discord_url&quot;:&quot;https://discord.gg/F359MXvM8r&quot;,&quot;github_url&quot;:&quot;&quot;,&quot;founded_at&quot;:&quot;2022-01-17&quot;,&quot;whitepaper_url&quot;:&quot;&quot;,&quot;description&quot;:&quot;## Highlights \r\n* Mecha Morphing is a fully decentralized, browser-based, play-to-earn ARPG game energized by the players in its Metaverse.\r\n* Mecha Morphing has five game modes for players to enjoy  - PVE, PVP Loot Mode, PVP Arena, Forging, and Bounty.\r\n*  The team has raised $2M from top VCs such as YGG SEA, IVC, Sfermion, Shima and GGG.\r\n\r\n## Product \r\n###  A sexy and realistic cyberpunk ARPG Metaverse. \r\n\r\nThe game is set in the future technological era with sci-fi cyberpunk as the background. Because of the plundering of precious resources, an international war broke out. Players enter the game as a member of the mecha squad called the “Rogue ones”, who save the world and maintain peace. \r\n\r\nMecha Morphing is a browser-based, play-to-earn game in which players earn $MMC or $MAPE by engaging and contributing to the ecosystem through the many game systems. Players can invest in NFTs in the form of weapons, mecha armor pieces, land and characters. They will be able to upgrade their character’s weapon and mecha NFTs in unlimited ways. Each piece will have unique stats, depending on the specific variables prior to forging.\r\n\r\n\r\n\r\n\u003cdiv class=\&quot;ratio ratio-16x9\&quot;\u003e\r\n  \u003ciframe src=\&quot;https://www.youtube.com/embed/cu-MMpHmAlk\&quot; title=\&quot;YouTube Mecha Morphing\&quot; allowfullscreen\u003e\u003c/iframe\u003e\r\n\u003c/div\u003e\r\n\r\n\r\n## Problem\r\n### Linear progression makes games tedious. \r\nMany play-to-earn games introduce similar issues of low playability or high barriers to enter. \r\n\r\n* **Low playability:** Many games have linear gameplay, requiring players to level up a character with the same task in order to advance. This often leads to players losing interest because it is not entertaining enough. \r\n\r\n* **High entry barriers:** Many games have a high threshold to enter. Players may need to buy multiple characters or items to just begin playing. This is not sustainable for the average player. This often turns the game into a place where only the more privileged can play. On top of this, many players also worry about their NFTs losing value as the game reaches the end of its life cycle. \r\n\r\n\r\n## Solution \r\n### Multiple game modes to keep players engaged.\r\n\r\nWith Mecha Morphing’s robust graphics and art, it aims to please the players visually while keeping them entertained with multiple game modes. There are many ways to play and pace through the game. \r\n\r\nSince the first character is given to every account, players can start checking out the Metaverse as soon as they open an account and can start betting on the arena right away. However, if a player would like to enter into any battles, all they need to invest in is a weapon. From there, they can enter into any fighting activities in the Metaverse.\r\n\r\nMecha Morphing NFTs are guaranteed to hold their value because new worlds will be consistently developed for the players. These new worlds will have new resources for the players to earn. The NFTs purchased in the original worlds will be transferable to the new worlds as well. \r\n\r\n![Mecha Morphing NFT](https://res.cloudinary.com/polkastarter/image/upload/v1642502265/projects/mecha-morphing/mm_nft.png) \r\n## Business Model\r\n### NFT sales and tickets sustain the ecosystem.\r\n\r\nMecha Morphing generates revenue by selling NFTs and collecting fees for upgrading NFTs and tickets in-game (arena battle tickets + ticket to enter new worlds).\r\n\r\nThe funds collected will be used to recruit more talents and keep developing the upcoming new Worlds. This includes new worlds for the browser game and an exciting VR first-person shooter (FPS) world. All 3D models of characters NFT, weapon NFT and mecha NFT will be wearable on the character in VR. \r\n\r\nThere will be different burning mechanisms within the game. A percentage of these burned tokens will be added to the community pool. \r\n\r\n## Market \r\n### Competition between play-to-earn games.\r\n\r\nThe main competition consists of other P2E, RPG games such as Nine Chronicles, Axie Infinity, Warena, and Dream Quest. However, many blockchain games have ecosystems that are built for the short term. The Mecha Morphing game system has a more sustainable ecosystem that is built for long-term engagement. New resources will still be available to new players as the players progress in the form of new worlds being consistently developed and released. There will also be a scholarship program, which ensures that there will always be active players in the game to keep the ecosystem sustained. \r\n\r\n\r\n## Investors  \r\n### The team has raised $2M from top VCs \r\n\r\n\r\nMecha Morphing has raised $2M from YGG SEA, IVC, Sfermioin, and Shima Capital, GGG and has partnered with XCV, Mask Network, and NFKings. There are also many strategic and marketing partners such as Innovion and BNPH that have guided the project on its successful path.\r\n\r\n![Mecha Morphing Investors](https://res.cloudinary.com/polkastarter/image/upload/v1642785917/projects/mecha-morphing/mm-partners.jpg) \r\n\r\n\r\n## Team \r\n### A team with extensive background in the crypto and game development industry  \r\n\r\n\r\n* **Justin Ma, Co-founder:**\r\nJustin is a tenacious serial entrepreneur with many start-ups under his belt. In particular, he has been in the blockchain industry for more than 6 years. He has experience in mining operations, fundraising, and infrastructure. He’s worked on projects such as Tron, Mask Network, Orchid protocol, to name a few. He is responsible for business development and sets the direction for the team. \r\n* **Jack Pan, Co-founder:**\r\nJack is an indie game producer that specializes in VR. Aside from his gaming experience, Jack has also worked as a consultant for technology in blockchain developing software for jobs.com and various oil companies. He is the driving force for the development behind our current browser game and our future VR version.\r\n* **Marilyn Vu, CMO:**\r\nMarilyn has a history of marketing in various industries, including gaming. She then moved into the crypto industry as a marketing manager with a blockchain consulting company before she was invited to take part in the Mecha Morphing project. She is responsible for the marketing strategy and community engagement at Mecha Morphing.\r\n\r\n## Token Utility \r\n###Mecha Morphing is operating on a dual-token-system\r\n\r\n$MMC is Mecha Morphing’s utility token and $MAPE its governance token. $MAPE token holders will be able to vote on proposals and share game revenue. $MAPE will be needed for various tickets in the metaverse. A ticket is necessary for every time a player enters an arena battle and a different type of ticket will be necessary to enter into future new worlds that will be created.   \r\n\r\nThe utility token, the $MMC, will underpin the entire gameplay system. Players will earn $MMC in drops through the game systems. Players will use the $MMC in the game from repairing their Mecha, betting on the arena PVP battles, or trading on the marketplace within the game. It is an integral part of the game used across all the mini-games in the Metaverse. Leveling up in the games will ensure that you earn more while you play. \r\n\r\n\r\nPlayers will also need to burn both of the tokens to mint new NFTs. New NFTs can be stronger than the ones in the current world or they can use it to upgrade existing NFTs. This will give a boost to the combat power, which leads to higher rewards during successful battles. \r\n\r\n![Mecha Morphing Token](https://res.cloudinary.com/polkastarter/image/upload/v1642451012/projects/mecha-morphing/mm_token.png)\r\n&quot;,&quot;legal_name&quot;:&quot;&quot;,&quot;ethereum&quot;:false,&quot;bsc&quot;:true,&quot;polygon&quot;:false,&quot;celo&quot;:false,&quot;application_form_url&quot;:null,&quot;kyc_form_url&quot;:&quot;&quot;,&quot;published_at&quot;:&quot;2022-01-18T11:24:11.897Z&quot;,&quot;applications_opened_at&quot;:&quot;2022-01-21T15:15:51.934Z&quot;,&quot;applications_closed_at&quot;:null,&quot;kyc_opened_at&quot;:null,&quot;kyc_closed_at&quot;:null,&quot;submitted_to_kyc_at&quot;:null,&quot;submitted_to_lottery_at&quot;:null,&quot;contracts_deployed_at&quot;:null,&quot;created_at&quot;:&quot;2022-01-17T20:24:54.715Z&quot;,&quot;updated_at&quot;:&quot;2022-02-10T00:07:13.954Z&quot;,&quot;permitted_events&quot;:[&quot;close_applications&quot;],&quot;permitted_states&quot;:[&quot;applications_closed&quot;],&quot;slug&quot;:&quot;mecha-morphing&quot;,&quot;short_description&quot;:&quot;Play to Earn Metaverse ARPG&quot;,&quot;ido_ends_at&quot;:null,&quot;ido_distributed_at&quot;:null,&quot;prospect_usd_token_price&quot;:0.4,&quot;prospect_initial_market_cap&quot;:312000.0,&quot;prospect_initial_token_circulation&quot;:780000.0,&quot;token_symbol&quot;:&quot;MAPE&quot;,&quot;ido_readiness_state&quot;:&quot;applications_open&quot;,&quot;pool_contracts_available&quot;:false,&quot;pool_contract_address&quot;:&quot;&quot;,&quot;pool_chain&quot;:&quot;&quot;,&quot;finalized&quot;:false,&quot;finalized_today&quot;:false,&quot;applications_open_at&quot;:null,&quot;applications_close_at&quot;:&quot;2022-02-20T13:00:00.000Z&quot;,&quot;pool_slug&quot;:&quot;&quot;,&quot;pool_progress&quot;:0,&quot;main_pool_id&quot;:null,&quot;pool_token_to&quot;:&quot;&quot;,&quot;pool_token_address&quot;:&quot;&quot;,&quot;pool_is_live&quot;:false,&quot;pool_is_upcoming&quot;:false,&quot;hidden&quot;:false}" />
