                            <div className="d-none" id="projectJson200" data-project-json="{&quot;id&quot;:200,&quot;name&quot;:&quot;Highstreet&quot;,&quot;state&quot;:&quot;kyc_closed&quot;,&quot;ido_starts_at&quot;:&quot;2021-10-11T13:00:00.000Z&quot;,&quot;min_allocation&quot;:null,&quot;max_allocation&quot;:420.0,&quot;total_raise&quot;:500000.0,&quot;logo_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/b8hp9tuojmjfwgcogi5zkuyrqv6v&quot;,&quot;card_background_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/jiq12ptcg86gphxhluu1b69sp33q&quot;,&quot;cover_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/h3bth4ctn32w62tmrk7a9cewyxn8&quot;,&quot;website_url&quot;:&quot;https://www.highstreet.market/&quot;,&quot;twitter_handle&quot;:&quot;@highstreetworld&quot;,&quot;telegram_handle&quot;:&quot;highstreetworld&quot;,&quot;twitter_url&quot;:&quot;https://twitter.com/@highstreetworld&quot;,&quot;telegram_url&quot;:&quot;https://t.me/highstreetworld&quot;,&quot;medium_url&quot;:&quot;https://medium.com/highstreet-market&quot;,&quot;linkedin_url&quot;:&quot;&quot;,&quot;discord_url&quot;:&quot;https://discord.gg/wxm6JNEbD9&quot;,&quot;github_url&quot;:&quot;&quot;,&quot;founded_at&quot;:&quot;2021-09-29&quot;,&quot;whitepaper_url&quot;:&quot;https://docsend.com/view/hk3z4fx6uygzkx29&quot;,&quot;description&quot;:&quot;## Highlights\r\n* Highstreet creates the Shopify experience on an MMORPG for brands\r\n* In an ultimate metaverse experience, users can easily set up shop and start interacting with products integrated as in-game objects.\r\n* Highstreet emphasizes hybrid marketplaces, where the rules of how a market should work can be rewritten based on the hottest goods.\r\n\r\n![Highstreet](https://res.cloudinary.com/polkastarter/image/upload/v1633078537/projects/highstreet/highstreet-overview.jpg) \r\n\r\n## Product \r\n###  Highstreet is the bridge between virtual worlds, real life, and the web\r\nHighstreet offers a hybrid-life system that aims to redefine how products are consumed and how markets sell their specific products. Highstreet is the place for new brands or celebrities to launch their products. The products on Highstreet’s market are crafted to exist both in physical and virtual form and always come straight from the brands. Highstreet offers a different take on building the metaverse, where the team develops everything in-house and targets high-quality immersive worlds that any brand would be happy to integrate with.\r\n![Highstreet Fomo's secret](https://res.cloudinary.com/polkastarter/image/upload/v1633078548/projects/highstreet/highstreet-product.jpg) \r\n\r\n## Problem\r\n### Liquidity and shipping issues for limited edition products.\r\nIssues such as illiquidity, logistical shipping nightmares, and counterfeits have historically been a problem in alt-asset investments since buyers need to not only obtain the product but also deal with finding a 3rd party buyer to offload the asset from them. Furthermore, secondary markets tend to soar many times higher than initial drop prices, and brands today are unable to capture any of that market. \r\n![Highstreet Problem](https://res.cloudinary.com/polkastarter/image/upload/v1633078537/projects/highstreet/highstreet-problem.jpg)\r\n\r\n## Solution \r\n### Highstreet Market is a specialized marketplace specifically created for limited edition products.\r\nAt Highstreet’s Market, all products start at an initial price. The price rises with every purchase and follows a predetermined curve that is customized based on the product. Every buyer gets an ERC-20 token that represents the product. They can then either wait for the price to go up and sell to make a profit or decide to redeem the physical product from the brand by burning the token. \r\n![Highstreet Ride the Wave with FOMO](https://res.cloudinary.com/polkastarter/image/upload/v1633078537/projects/highstreet/highstreet-solution.jpg) \r\n\r\n## Business Model\r\n### Highstreet raised $5 million to accelerate its go-to-market time \r\nWith “phygital” products, Highstreet provides brands with new revenue streams and marketing channels. Tokenized products with digital components introduce a new way to sell without decreasing inventory up-front. \r\n\r\n## Market \r\n### Many metaverse projects are fighting for relevance, with the main competitor being Decentraland.\r\nHighstreet offers a different take on the metaverse concept through developing everything in-house and targeting high-quality immersive worlds that any brand would love to integrate with.\r\n\r\n## Investors  \r\n### Highstreet raised $5 million.\r\nNotable investors include Mechanism Capital, NGC Ventures, GBV, Panony, Palm Drive Capital, Cherubic Ventures, Jump Trading, and Shima Capital.\r\n![Highstreet Investors](https://res.cloudinary.com/polkastarter/image/upload/v1633254401/projects/highstreet/highstreet-investors.jpg) \r\n\r\n## Team \r\n### Extensive experience serving retail customers with Mixed Reality.\r\n * Travis Wu (Co-Founder)\r\n* Jenny Guo (Co-Founder)\r\n* Flora Fang\r\n* Gary Mao\r\n* Jack Qiao\r\n* Matt Conn\r\n\r\n## Token Utility\r\n### Highstreet utilizes a two-token system. The first to be launched is the $HIGH token.\r\nThis is the native governance token and will be used to buy properties and products within the market. At a later stage, Street Cred, a token for day-to-day in-game functions, will be introduced.\r\n\r\n&quot;,&quot;legal_name&quot;:&quot;&quot;,&quot;ethereum&quot;:false,&quot;bsc&quot;:true,&quot;polygon&quot;:false,&quot;celo&quot;:null,&quot;application_form_url&quot;:null,&quot;kyc_form_url&quot;:&quot;http://highstreet.synaps.io&quot;,&quot;published_at&quot;:&quot;2021-09-30T10:25:41.132Z&quot;,&quot;applications_opened_at&quot;:&quot;2021-10-03T18:49:05.610Z&quot;,&quot;applications_closed_at&quot;:&quot;2021-10-03T18:49:40.488Z&quot;,&quot;kyc_opened_at&quot;:&quot;2021-10-03T18:50:24.513Z&quot;,&quot;kyc_closed_at&quot;:&quot;2021-11-04T08:01:06.659Z&quot;,&quot;submitted_to_kyc_at&quot;:&quot;2021-10-03T18:50:21.282Z&quot;,&quot;submitted_to_lottery_at&quot;:null,&quot;contracts_deployed_at&quot;:null,&quot;created_at&quot;:&quot;2021-09-29T14:21:48.924Z&quot;,&quot;updated_at&quot;:&quot;2021-11-26T10:17:04.995Z&quot;,&quot;permitted_events&quot;:[&quot;open_kyc&quot;],&quot;permitted_states&quot;:[&quot;kyc_open&quot;],&quot;slug&quot;:&quot;highstreet&quot;,&quot;short_description&quot;:&quot;Shopify on an MMORPG &quot;,&quot;ido_ends_at&quot;:&quot;2021-10-14T16:00:00.000Z&quot;,&quot;ido_distributed_at&quot;:&quot;2021-10-14T16:00:00.000Z&quot;,&quot;prospect_usd_token_price&quot;:1.0,&quot;prospect_initial_market_cap&quot;:null,&quot;prospect_initial_token_circulation&quot;:null,&quot;token_symbol&quot;:&quot;HIGH&quot;,&quot;ido_readiness_state&quot;:&quot;ready&quot;,&quot;pool_contracts_available&quot;:true,&quot;pool_contract_address&quot;:&quot;0xf77A7Fc4BD1C6714F4Fc6fb4538A1159cd93D5C6&quot;,&quot;pool_chain&quot;:&quot;binance&quot;,&quot;finalized&quot;:true,&quot;finalized_today&quot;:false,&quot;applications_open_at&quot;:null,&quot;applications_close_at&quot;:null,&quot;pool_slug&quot;:&quot;highstreet-binance&quot;,&quot;pool_progress&quot;:100,&quot;main_pool_id&quot;:288,&quot;pool_token_to&quot;:&quot;BNB&quot;,&quot;pool_token_address&quot;:&quot;0x5f4bde007dc06b867f86ebfe4802e34a1ffeed63&quot;,&quot;pool_is_live&quot;:false,&quot;pool_is_upcoming&quot;:false,&quot;hidden&quot;:false}" />
