                            <div className="d-none" id="projectJson187" data-project-json="{&quot;id&quot;:187,&quot;name&quot;:&quot;Orion Money&quot;,&quot;state&quot;:&quot;published&quot;,&quot;ido_starts_at&quot;:&quot;2021-09-29T13:00:00.000Z&quot;,&quot;min_allocation&quot;:0.0,&quot;max_allocation&quot;:0.0,&quot;total_raise&quot;:400000.0,&quot;logo_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/aovnyvd72hvhc5l8ab2e9404jq0h&quot;,&quot;card_background_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/nfy5nnqh2v55q1dbfcynrqeipzcb&quot;,&quot;cover_url&quot;:&quot;https://storage.googleapis.com/polkastarter-production-assets/t4znn9t7h13dxeiph95ds7cf60en&quot;,&quot;website_url&quot;:&quot;https://orion.money/&quot;,&quot;twitter_handle&quot;:&quot;@orion_money&quot;,&quot;telegram_handle&quot;:&quot;Orion_Money&quot;,&quot;twitter_url&quot;:&quot;https://twitter.com/@orion_money&quot;,&quot;telegram_url&quot;:&quot;https://t.me/Orion_Money&quot;,&quot;medium_url&quot;:&quot;&quot;,&quot;linkedin_url&quot;:&quot;&quot;,&quot;discord_url&quot;:&quot;&quot;,&quot;github_url&quot;:&quot;&quot;,&quot;founded_at&quot;:&quot;2021-09-21&quot;,&quot;whitepaper_url&quot;:&quot;https://orion-money.medium.com/orion-money-litepaper-release-7e98bb3acb6f&quot;,&quot;description&quot;:&quot;## Highlights\r\n* Orion Money is a service developed to be a place where stablecoin holders can get a safe and secure yield of 20-25% APY in DeFi.\r\n* Orion Money brings high yields from other chains such as Terra to users on Ethereum through an easy decentralized application.\r\n* Orion Money managed to Raise $4.2 million Between seed and community rounds.\r\n\r\n## Product \r\n### Orion Money is a product that provides fixed 13.5% APY on USDT, USDC, DAI, BUSD, as well as 16.5% APY on wUST. \r\nThere are plans to increase this APY to 20-25% in the future after TGE. Additionally, users can make simple deposits, and Orion Money has passed three smart contract audits with amazing results while also managing to provide two insurance plans.\r\n\r\n![Orion Money Vision](https://res.cloudinary.com/polkastarter/image/upload/v1632178793/projects/orion-money/orion-product.jpg) \r\n\r\n## Problem \r\n### There is a $120 billion market cap of stablecoins, with the majority of them on Ethereum. \r\nStablecoin yield is low and volatile. This being the case, stablecoin holders require a way through which they can get a high and fixed rate, where they can move their stablecoins to a blockchain with ease.\r\n\r\n## Solution\r\n### Orion Money has developed a decentralized application which is known as Orion Saver. \r\nThis dApp offers users a high level of convenience when it comes to earning high yields on their stablecoin deposits. The idea here is for all of it to be done without needing to move assets across different blockchains manually. Orion Money also has high fixed yields from new chains such as Terra to Ethereum, as well as other protocols where they can easily make their deposits, earn high yields, and receive interest back in their original stablecoins.\r\n\r\n## Business Model\r\n### The Orion Money Ecosystem generates revenue from the dApp itself and brings value to the native token, ORION. \r\nThe majority of the dApp's initial revenue comes from the yield differential between what Orion Money receives from Anchor Protocol and what it has to pay to its users. The full net revenue will be used to buy ORION tokens on the open market, and this will be added into a Staking Fund. Furthermore, Orion Money has a validator on the Terra chain with $700m of Luna staked on it and will start charging a 5% commission at TGE. The commission will go to ORION token stakers.\r\n\r\n## Market \r\n### Our main competitors are Curve, Aave, and PancakeSwap.\r\nCompared to our competitors, we offer yield rates that are higher than all other DeFi and CeFi savings and lending dApps. Orion Saver is also a decentralized and smart contract-based dApp, where other savings protocols can be integrated into Orion Money as a yield resource.\r\n\r\n![Orion Money Competitors](https://res.cloudinary.com/polkastarter/image/upload/v1632179040/projects/orion-money/orion-competitors.jpg)\r\n\r\n## Investors \r\n### We have raised $4.2 million from top VCs, cryptocurrency projects, and communities.\r\nThis was raised from several top entities, including Delphi Digital, Hashed, Accomplice, SkyVision Capital, and the Orion Money Community farming.\r\n![Orion Money Investors](https://res.cloudinary.com/polkastarter/image/upload/v1633212417/projects/orion-money/orion-investors.png) \r\n\r\n## Team\r\n![Orion Money Team](https://res.cloudinary.com/polkastarter/image/upload/v1632178794/projects/orion-money/orion-team.jpg)\r\n\r\n## Token Utility\r\n### $ORION is the native cryptocurrency token used by Orion Money’s users and investors. \r\nThis token can be staked through which the users can maximize their benefits. Stablecoin depositors get boosted yields depending on the number of ORION tokens they have staked. Investors or speculators are incentivized to stake ORION tokens to maximize the value of the tokens. \r\n\r\n![Orion Money Token](https://res.cloudinary.com/polkastarter/image/upload/v1632178947/projects/orion-money/orion-token.jpg)&quot;,&quot;legal_name&quot;:&quot;&quot;,&quot;ethereum&quot;:true,&quot;bsc&quot;:false,&quot;polygon&quot;:false,&quot;celo&quot;:null,&quot;application_form_url&quot;:null,&quot;kyc_form_url&quot;:&quot;&quot;,&quot;published_at&quot;:&quot;2021-09-20T23:06:51.346Z&quot;,&quot;applications_opened_at&quot;:null,&quot;applications_closed_at&quot;:null,&quot;kyc_opened_at&quot;:null,&quot;kyc_closed_at&quot;:null,&quot;submitted_to_kyc_at&quot;:null,&quot;submitted_to_lottery_at&quot;:null,&quot;contracts_deployed_at&quot;:null,&quot;created_at&quot;:&quot;2021-09-20T22:41:59.224Z&quot;,&quot;updated_at&quot;:&quot;2021-11-26T10:16:49.735Z&quot;,&quot;permitted_events&quot;:[&quot;unpublish&quot;],&quot;permitted_states&quot;:[&quot;draft&quot;],&quot;slug&quot;:&quot;orion-money&quot;,&quot;short_description&quot;:&quot;Cross-chain stablecoin bank&quot;,&quot;ido_ends_at&quot;:&quot;2021-09-30T12:30:00.000Z&quot;,&quot;ido_distributed_at&quot;:&quot;2021-09-30T12:30:00.000Z&quot;,&quot;prospect_usd_token_price&quot;:0.0,&quot;prospect_initial_market_cap&quot;:0.0,&quot;prospect_initial_token_circulation&quot;:0.0,&quot;token_symbol&quot;:&quot;ORION&quot;,&quot;ido_readiness_state&quot;:&quot;ready&quot;,&quot;pool_contracts_available&quot;:true,&quot;pool_contract_address&quot;:&quot;0x2be1C2E4aF39b335f15c3E682683156664EAb2Dd&quot;,&quot;pool_chain&quot;:&quot;ethereum&quot;,&quot;finalized&quot;:true,&quot;finalized_today&quot;:false,&quot;applications_open_at&quot;:null,&quot;applications_close_at&quot;:null,&quot;pool_slug&quot;:&quot;orion-money-2&quot;,&quot;pool_progress&quot;:100,&quot;main_pool_id&quot;:255,&quot;pool_token_to&quot;:&quot;ETH&quot;,&quot;pool_token_address&quot;:&quot;0x727f064a78dc734d33eec18d5370aef32ffd46e4&quot;,&quot;pool_is_live&quot;:false,&quot;pool_is_upcoming&quot;:false,&quot;hidden&quot;:false}" />
