                                        <div
                                            className="d-none"
                                            id="projectJson57"
                                            data-project-json='{"id":57,"name":"ArtMeta","state":"kyc_closed","ido_starts_at":"2022-01-27T13:00:00.000Z","min_allocation":null,"max_allocation":1000.0,"total_raise":500000.0,"logo_url":"https://storage.googleapis.com/polkastarter-production-assets/g1lh0pka1buydth3h0k38nk2t51b","card_background_url":"https://storage.googleapis.com/polkastarter-production-assets/54tb6ki60chdr3lip392cduk66zl","cover_url":"https://storage.googleapis.com/polkastarter-production-assets/qx8iy0cl2ptjxyyjnnksl24wyd7b","website_url":"https://artmeta.io/","twitter_handle":"Artmetaio","telegram_handle":"artmeta_io","twitter_url":"https://twitter.com/Artmetaio","telegram_url":"https://t.me/artmeta_io","medium_url":"","linkedin_url":"https://www.linkedin.com/company/artmetaio","discord_url":"https://discord.com/invite/WQruhujPnV","github_url":"","founded_at":"2022-01-10","whitepaper_url":"","description":"## Highlights \r\n* ArtMeta is a stunning, photo-realistic, fine-art focused metaverse resource and\r\nExperience.\r\n* With ArtMeta, galleries will soon be able to rent virtual spaces and showcase\r\ntheir artists’ work in the Art Metaverse.\r\n* ArtMeta connects first-class international art world participants in a visionary,\r\nbeautifully rendered digital world, creating a unique new paradigm for art sales in the emerging and fast-growing digital art market.\r\n\r\n\r\n## Product \r\n### Experience fine art immortalized on the blockchain like never before\r\n\r\n\r\nArtMeta is a hyper-realistic virtual world, based on an imaginary island conceived by acclaimed painter Jonathan Delachaux, executed in collaboration with Romain Donnot, a Live FX and digital visionary. The island itself is a work of art that has been 11 years in the making. Each element within the island has a purpose, meaning, and story. \r\n\r\nDelachaux has worked with a geologist, biologist, volcanologist, and astrologist to ensure the elements, details, and configuration of the island are congruent with nature. He also worked with an economist, and an ex-UN diplomat on the logistics of society, how it should function and how one may become part of it. \r\nThe purpose of creating this state-of-the-art metaverse is to eliminate the boundaries of the physical world, giving accomplished creators the means to fully express their artistic vision and reveal new possibilities. \r\n\r\nIn this beautiful place, all the doors will be open and visitors will be able to experience endless artist- and gallery-created places both indoors and outdoors, with regular live events as well as high-end exhibitions. \r\n\r\nA perfect mix of Soho, Art Basel, and the Venice Biennale, art enthusiasts will be able to purchase extremely rare NFT-based artwork from world-class art galleries and artists.\r\n\r\n ![ArtMeta Product](https://res.cloudinary.com/polkastarter/image/upload/v1641822494/projects/artmeta/product-1.png) \r\n\r\n## Problem\r\n### Artists and first-class art galleries worldwide are still struggling to get accustomed to the metaverse, lagging behind and losing massive revenue streams\r\n\r\nAs the NFT market expands exponentially, traditional artists are learning to understand the urgency of a permanent presence in a high-quality digital art environment, but they lack the basic knowledge of how to make that happen. \r\n\r\nTraditional art galleries are overwhelmed with the speed at which the digital space is moving and need help adapting to a rapidly growing multi-billion-dollar market. The digital community is beginning to understand that important digital art and NFTs with high artistic value and growth potential require education and careful selection.\r\n\r\n\r\n## Solution \r\n### ArtMeta connects artists and collectors in a visionary, beautifully rendered digital universe, through a unique new paradigm for displaying and selling art in an emerging and fast-growing market.\r\n\r\nArtMeta opens up new marketing and trading opportunities for well-established artists and tier 1 galleries of the traditional art market by reaching out to countless new buyer groups and its collectors in the digital space.\r\n\r\nMore than just a metaverse, ArtMeta is a comprehensive platform designed to give artists and galleries the tools they need to participate in the thriving NFT market. Through these tools, ArtMeta aims to unite the world of fine art and crypto, creating a viable digital economy to support them in perpetuity.\r\n\r\n ![ArtMeta Product 2](https://res.cloudinary.com/polkastarter/image/upload/v1641822624/projects/artmeta/product-2.png) \r\n\r\n## Business Model\r\n### Onboarding art galleries, generating exclusive NFTs for the art world, presenting and selling them directly in the ArtMeta metaverse \r\n\r\nArtMeta will onboard galleries, who will be active advocates promoting the project and metaverse. Each gallery, in partnership with their artists, will have the freedom to design their own space within ArtMeta, giving accomplished creators the means to fully express their artistic vision and reveal new possibilities. Users will be able to explore the gallery spaces in an untraditional and unexpected way, without the boundaries of traditional gallery white walls. Users will be able to learn about prevalent artists and purchase their ultra-rare NFTs directly in the ArtMeta metaverse with the $MART token.\r\nArtMeta will charge galleries rent fees as well as earn a percentage from each NFT sale.\r\n\r\n\r\n \r\n## Investors  \r\n### $1.75M raised in seed and private rounds from top VCs\r\n\r\n\r\nVCs of the initial seed round will be disclosed at a later date. ArtMeta is in process of onboarding additional investors, as well as finalizing details of the token listing with several leading DEXs and platforms. ArtMeta’s previous public raise of $750K on TrustSwap was 10x oversubscribed.\r\n\r\n\r\n## Team \r\n### The perfect blend of conceptual artists, builders and technologists \r\n\r\n**Roger Haas, CEO, Head of Sourcing**\r\n* Former acting director @ Art Stations Foundation / Muzeum Susch, 2020\r\n* Founder and director @ offspace online, 2011-2019\r\n* Founder and director @ Haas \u0026 Fischer Gallery, Zürich 2005-2011\r\n\r\n\r\n**Romain Donnot, Head of Platform Development**\r\n* Co-Creator Art Metaverse\r\n* Founder, CEO @ N’Break, Saint-Julien (FR)\r\n* Virtual Production Supervisor, Live FX Supervisor\r\n* Head of Post-Production, Volumetric Camera Systems, Inc, Vancouver Canada with Neill Blomkamp (Director) for “Demonic” 2021 (Netflix Production)\r\n\r\n\r\n**Jonathan Delachaux, Artistic Director**\r\n* Co-Creator Art Metaverse\r\n* Visual Artist (graduate from HEAD Geneva)\r\n\r\n\r\n## Token Utility \r\n### $MART: The centerpiece of the ArtMeta world\r\n\r\nThe $MART token utility is split between pre-launch and post-launch utility. Leading up to the launch, users will be able to stake their $MART tokens to participate in ArtMeta NFT initiatives, win exclusive NFTs, along with VIP and early access to shows, events and exhibitions, where $MART token can be used as well.\r\n\r\nFurther post-launch token utilities will be: \r\n* Medium of exchange in the Metaverse. In order to rent land, you need $MART tokens. The token grants you access to several features within the Metaverse. 50% of those will be burned. To show the potential of this utility: Art Basel (the world&apos;s leading Art Fair) generates $93M annual revenue. If ArtMeta gains 10% of this market in the digital space, with a 50% burn rate, it will buy back and burn $4.7M worth of $MART tokens every year.\r\n* $MART tokens for the generation of any non-art items (name, clothing, visa, etc) acquired in the ArtMeta metaverse will be burned.\r\n* $MART tokens will be used for governance in the future.\r\n","legal_name":"","ethereum":false,"bsc":false,"polygon":true,"celo":false,"application_form_url":null,"kyc_form_url":"https://go.fractal.id/artmeta","published_at":"2022-01-11T12:53:06.137Z","applications_opened_at":"2022-01-13T14:57:39.273Z","applications_closed_at":"2022-01-20T13:29:13.506Z","kyc_opened_at":"2022-01-21T11:00:15.513Z","kyc_closed_at":"2022-01-26T14:46:16.498Z","submitted_to_kyc_at":"2022-01-21T10:49:33.512Z","submitted_to_lottery_at":null,"contracts_deployed_at":null,"created_at":"2022-01-10T10:09:00.443Z","updated_at":"2022-01-27T14:05:03.685Z","permitted_events":["open_kyc"],"permitted_states":["kyc_open"],"slug":"artmeta","short_description":"Society created by artists","ido_ends_at":"2022-01-27T15:00:00.000Z","ido_distributed_at":"2022-03-03T15:33:00.000Z","prospect_usd_token_price":0.3,"prospect_initial_market_cap":1037500.0,"prospect_initial_token_circulation":3458333.0,"token_symbol":"MART","ido_readiness_state":"ready","pool_contracts_available":true,"pool_contract_address":"0x251fD52525466d8AD9Fc401442254C27ee716DB2","pool_chain":"polygon","finalized":true,"finalized_today":false,"applications_open_at":"2022-01-14T13:00:00.000Z","applications_close_at":"2022-01-20T13:00:00.000Z","pool_slug":"art-meta-polygon","pool_progress":100,"main_pool_id":314,"pool_token_to":"MATIC","pool_token_address":"0xb94407dec28e8dd43a8f1171fc2ec97b3813ed6a","pool_is_live":false,"pool_is_upcoming":false,"hidden":false}'
                                        ></div>
                                        <div
                                            className="d-none"
                                            id="projectJson55"
                                            data-project-json='{"id":55,"name":"SYN CITY","state":"kyc_closed","ido_starts_at":"2022-01-20T13:00:00.000Z","min_allocation":null,"max_allocation":400.0,"total_raise":500000.0,"logo_url":"https://storage.googleapis.com/polkastarter-production-assets/vmfcmmb9ro32xdetcij43m2o32so","card_background_url":"https://storage.googleapis.com/polkastarter-production-assets/o3ruopbdjpxclq0w0pzpnr8auae8","cover_url":"https://storage.googleapis.com/polkastarter-production-assets/c3bvrnbaru2lh6mx8faux39mzj3d","website_url":"https://syn.city/","twitter_handle":"SynCityHQ","telegram_handle":"SynCityHQ","twitter_url":"https://twitter.com/SynCityHQ","telegram_url":"https://t.me/SynCityHQ","medium_url":"","linkedin_url":"","discord_url":"http://discord.gg/tSVtRkppnp","github_url":"","founded_at":"2021-12-25","whitepaper_url":"","description":"## Highlights \r\n* SYN CITY is the first-ever Mafia Metaverse. Here you fight, loot, build and govern through your own syndicate.  \r\n* $SYNR is the governance token for the metaverse and core of the Mafia-as-a-DAO system. Users stake $SYNR to generate votes to delegate to any syndicate leaders. A limited number of the highest voted leaders will be on the High Table to govern the earning pools of the metaverse. \r\n* $8 million invested by Angels such as Justin Kan (Co-founder of Twitch), Do Kwon (Co-founder of Terra), Kieran Warwick (Co-founder of Illuvium), Santiago R Santos (Ex-Partner of Parafi Capital), leading VCs including Animoca Brands, Spartan Group and HOF and strategic partners including Overwolf, Merit Circle, YGG, GuildFi and many more. \r\n\r\n\r\n\r\n## Product \r\n### Be the boss and take control of turfs, business, loot, and raid other syndicates to expand your empire.\r\n\r\nSYN CITY is the first-ever Mafia Metaverse. Here you fight, loot, build and lead. Here you join a syndicate and run businesses, tax your underlings and pay tribute to your bosses through blockchain-backed assets. With enough Ambition, Power, and Swagger you can sit at the head of a syndicate and help chart the course of the Mafia Metaverse.\r\n\r\nDisplaying a stylized take on the degen and underworld, SYN CITY takes this concept a step further by making the game *free to play* where you can *play and earn*. Strategize to build a ruthless empire backed by a gritty syndicate of peers, friends, and enemies alike. The Mafia will allow #synners to grind their way up through daily missions, events, battles, trades, farms, and much more.\r\n\r\n\r\n\r\n\r\n\u003cdiv class=\"ratio ratio-16x9\"\u003e\r\n  \u003ciframe src=\"https://www.youtube.com/embed/eDWg_C4v3rQ\" title=\"YouTube SYN CITY\" allowfullscreen\u003e\u003c/iframe\u003e\r\n\u003c/div\u003e\r\n\r\n\r\n\r\n\r\n\r\n## Problem\r\n### The barriers to entry into the PLAY \u0026 EARN model are high and there is limited incentives for the players involved\r\n\r\nMost blockchain game economy designs are not working. For starters, it’s not fair to ask users to pay over $1000 before they can even enter and play the game. The result of this is that users tend to earn back their initial cost, they withdraw and sell the assets immediately to recover from the initial investments. This is a death spiral. \r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n## Solution \r\n### Everyone can be a part of and thrive within the metaverse by bringing the FREE TO PLAY into PLAY TO EARN\r\n\r\nSYN CITY is building a mafia metaverse with the following features: \r\n\r\n* Free to play and earn: anyone can come and engage, no ownership of NFT needed\r\n* Multiple revenue streams: users would have multiple revenue streams by engaging from grinding through events, re-allocating assets to generate returns, and lead your syndicate to earn like a mob through the MaaD system.\r\n* Focus on the mafia vertical, making it easy for people to click and engage and easy to form a consensus.  \r\n\r\n ![Syn City Solution](https://res.cloudinary.com/polkastarter/image/upload/v1640448544/projects/syn-city/NFT_DROP_Logo_960_x_540_px_960_x_540_px_q3yr38.gif) \r\n\r\n## Business Model\r\n### Be the leader you have always wanted to be by creating, taking charge, and leading a group of degenerate syndicate members. Raid loot, layout strategies, expand your crews, and collect tax.\r\n\r\nThe Mafia-as-a-DAO (MaaD) consists of leaders on the High Table that manage the governance committee. Users are incentivized and SYN CITY revenue is generated by the MaaD system.\r\n\r\n* Players stake $SYNR to generate $sPOWER and delegate that to the syndicate leaders. There are unlimited syndicates with unlimited leaders, but only top 7 leaders with the most $sPOWER will be allowed to join the High Table to manage the governance committee which shapes the earnings events and allocate the earnings pool.  \r\n\r\n* Syndicate leaders could offer benefits in assets, special events, and many others to the members of their syndicates for recruitment, engagement and loyalty. \r\n\r\n* Syndicate leaders can tax their members for the benefits offered. The SYN CITY then platform charges a tax to the syndicates, out of which a portion goes to the overall community as Universal Basic Income (UBI), rest goes to the developers. \r\n\r\n ![Syn City Business Model](https://res.cloudinary.com/polkastarter/image/upload/v1640448781/projects/syn-city/image4_q5yk0c.png) \r\n\r\n## Market \r\n### This is the crypto era and everyone should have an equal right to be able to play and earn. In the Mafia Metaverse, it should be FREE TO EARN.\r\n\r\nIt’s not fair to ask people to pay $1000 hard-earned money before they can play or earn from the game. The Free to PLAY to EARN model ensures there is a global market base with a large funnel. And by building for both mobile and desktop, SYN CITY is ready for mass adoption. \r\n\r\n ![Syn City Market](https://res.cloudinary.com/polkastarter/image/upload/v1640448780/projects/syn-city/image2_h6ak7j.png) \r\n\r\n\r\n \r\n## Investors  \r\n### Mafia and underworld syndicates is one of the few evergreen genres among all the entertainment products worldwide. SYN CITY is boosted by web2 and web3 pioneers \r\n\r\n$8 Million Invested by Justin Kan (Co-founder of Twitch), Do Kwon (Co-founder of Terra), Alex Pack (Co-founder of DragonFly), Luke Wagman (Co-founder of Coinmarketcap), Jordan Momtazi (Co-founder of Synthetix), Kieran Warwick (Co-founder of Illuvium), Elliot Wainman (Co-founder of ElliotTrade \u0026 Superfarm), Alex Becker (Co-founder of Neo Tokyo Identity), Santa-iago R Santos (Ex-Partner of Parafi Capital), Paul Menchov (Co-founder of Coinlist and Republic), also leading VC including Animoca Brands, Spartan Group, HOF, iAngels, A\u0026T Capital and strategic partners including Overwolf, Merit Circle, YGG, GuildFi and many more. \r\n\r\n\r\n ![Syn City Investors](https://res.cloudinary.com/polkastarter/image/upload/v1640448537/projects/syn-city/existing_investors_ghvafi.png) \r\n\r\n\r\n## Team \r\n### SYN CITY is developed by a team of industry experts from Disney, Ubisoft, Roblox, EA, Gameloft, Google, Yahoo and Twitter.\r\n\r\n* **Roy Liu (Co-founder):** Former head of business and marketing of TRON for the last 4 years. Early member of PopCap Game back in 2006, focused on the product of several award-winning game titles like Plants VS Zombies, Bejeweled, etc. PopCap later sold to EA for $1.3B in 2011. In 2012, joined Chartboost, the biggest mobile game advertising platform at the time. Chartboost later sold to Zynga for $250M.\r\n* **Mihai Pohontu (Co-founder):**\r\nFormer VP at Samsung Electronics America. Founder and General Manager of EA Romania, one of the largest game studios in Eastern Europe. Previously overseeing the operational functions of Disney&apos;s interactive entertainment portfolio and managing Electronic Arts&apos; multi-disciplinary development services organization.\r\n\r\n\r\n ![Syn City Team](https://res.cloudinary.com/polkastarter/image/upload/v1640448780/projects/syn-city/image1_gpjydw.png) \r\n\r\n\r\n\r\n## Token Utility \r\n### $SYNR is the governance token for the metaverse and core of the Mafia-as-a-DAO system. \r\n\r\n* Stake $SYNR to generate $sPOWER and delegate that to the syndicate leader. \r\n\r\n* Stake $SYNR to earn additional yield.\r\n\r\n* $SYNR is also utilized by staking to earn $sSYNR (synthetic SYNR) which is used to purchase in-game assets on the marketplace with discount.\r\n\r\n\r\n ![Syn City Token](https://res.cloudinary.com/polkastarter/image/upload/v1640448780/projects/syn-city/image5_zzbmmy.png) ","legal_name":"","ethereum":true,"bsc":false,"polygon":false,"celo":false,"application_form_url":null,"kyc_form_url":"https://syncity.synaps.me/","published_at":"2021-12-25T21:00:49.524Z","applications_opened_at":"2021-12-27T21:04:18.138Z","applications_closed_at":"2022-01-04T12:21:11.055Z","kyc_opened_at":"2022-01-04T22:17:29.915Z","kyc_closed_at":"2022-01-19T00:55:00.540Z","submitted_to_kyc_at":"2022-01-04T22:17:22.907Z","submitted_to_lottery_at":null,"contracts_deployed_at":null,"created_at":"2021-12-25T15:54:25.786Z","updated_at":"2022-01-20T20:46:26.556Z","permitted_events":["open_kyc"],"permitted_states":["kyc_open"],"slug":"syn-city","short_description":"The Mafia Metaverse","ido_ends_at":"2022-01-20T20:00:00.000Z","ido_distributed_at":"2022-01-25T17:00:00.000Z","prospect_usd_token_price":0.02,"prospect_initial_market_cap":null,"prospect_initial_token_circulation":null,"token_symbol":"SYNR","ido_readiness_state":"ready","pool_contracts_available":true,"pool_contract_address":"0x79145B511e7B46b42e450b2aB8b8e0Dff9f71B78","pool_chain":"ethereum","finalized":true,"finalized_today":false,"applications_open_at":null,"applications_close_at":"2022-01-04T12:00:00.000Z","pool_slug":"syn-city-ethereum","pool_progress":100,"main_pool_id":313,"pool_token_to":"ETH","pool_token_address":"0xbc6E06778708177a18210181b073DA747C88490a","pool_is_live":false,"pool_is_upcoming":false,"hidden":false}'
