                                            className="d-none"
                                            id="projectJson45"
                                            data-project-json='{"id":45,"name":"Galaxy Fight Club","state":"kyc_closed","ido_starts_at":"2022-01-06T14:00:00.000Z","min_allocation":null,"max_allocation":500.0,"total_raise":2000000.0,"logo_url":"https://storage.googleapis.com/polkastarter-production-assets/3wdekv3g703ycx5cwvetq983tk90","card_background_url":"https://storage.googleapis.com/polkastarter-production-assets/ki5r3s85re366pfuuw60k6vax5pk","cover_url":"https://storage.googleapis.com/polkastarter-production-assets/yuqu9ztx5ljqvexgtfc0o0h8klss","website_url":"https://galaxyfightclub.com/","twitter_handle":"GalaxyFight_NFT","telegram_handle":"galaxyfightclub_nft","twitter_url":"https://twitter.com/GalaxyFight_NFT","telegram_url":"https://t.me/galaxyfightclub_nft","medium_url":"https://medium.com/@Galaxyfightclub/","linkedin_url":"","discord_url":"https://discord.com/invite/kWJZJAYg2C","github_url":"","founded_at":"2021-11-25","whitepaper_url":"","description":"## Highlights\r\n* The Galaxy Fight Club is a real-time PvP game bringing together different NFT collections on one single platform.\r\n* On Galaxy Fight Club, users &apos;login&apos; and battle each other with their NFT characters\r\n\r\n\r\n## Product\r\n### The first cross-IP PvP game for the NFT Universe\r\nGalaxy Fight Club (GFC) is the first cross-IP, cross-platform (PC \u0026 Mobile) PvP fighting game, where holders of different NFT collections can battle with each other to both win and earn rewards through play-to-earn mechanics. It has a fast-paced MOBA gameplay that is mobile-native and similar to Brawl Stars. Each 3v3 match is around 5 minutes long and the team that gets 20 kills first wins the match.\r\n\u003cdiv class=\"ratio ratio-16x9\"\u003e\r\n  \u003ciframe src=\"https://www.youtube.com/embed/rzZIvkAHnJg\" title=\"YouTube Galaxy Fight Club\" allowfullscreen\u003e\u003c/iframe\u003e\r\n\u003c/div\u003e\r\n\r\n## Problem\r\n### Siloed NFT collections and lack of Cross-IP elements\r\nIP interoperability is a key attribute to a functional Metaverse \u0026 NFTs are designed to be composable Lego bricks for others to build upon. For example, the most popular NFT projects today such as BAYC, Loot adventure gear or more recently Cryptoadz, all have one commonality, which is that they all positioned themselves as a Lego brick to be built upon. BAYC was the first project to give their holders full commercial rights, which allowed a second layer of derivative work to flourish; Loot was the first project at the time to launch just a list of text/words and invite others to give it meaning and interpretation. More recently, Cryptoadz was one of the first projects to give its users public domain licenses to their NFTs. In short, NFTs are designed in a way for other creators and builders to build something on top of them.\r\n\r\nWhen it comes to games like Super Smash Bros, the reason that they are so fun is that characters from different IPs are coming together and players can for example pick Pikachu to fight Mario. It&apos;s this cross-IP element that makes these games enjoyable to play over the years. Yet the current NFT landscape is still very siloed. Making use of the interoperability of digital items/assets in a meaningful way is key to creating a long-lasting functioning NFT ecosystem.\r\n\r\n\r\n## Solution\r\n### Providing utility for NFT collections\r\nNFT holders are looking for a way to ‘use’ their NFTs. Galaxy Fight Club will fill this gap by being the first cross-IP PvP game platform in the NFT Universe.\r\n![Galaxy Fight Club Earning system](https://res.cloudinary.com/polkastarter/image/upload/v1638227998/projects/galaxy-fight-club/gfc-earning-system.png)\r\n\r\n## Business Model\r\n### Sustainable revenue through royalties and NFT drops\r\nGFC will generate revenue through two channels. The first is by taking a 5% royalty from all secondary trades in a similar fashion to the Axie Infinity model. The second channel is through NFT drops. Together these two revenue channels will be enough for GFC to be self-sustaining in the medium to long term. The initial capital raised from the strategic sale and the IDO will help GFC accelerate development and user acquisition through paid advertising. GFC plans to hire more game artists, coders and developers as well as onboard a CMO and COO. The current war chest allows for a runway of around 36 months.\r\n![Galaxy Fight Club Business Model](https://res.cloudinary.com/polkastarter/image/upload/v1638227852/projects/galaxy-fight-club/gfc-business-model.png)\r\n\r\n## Market\r\n### Building the metaverse from the bottom-up\r\nThere are a few projects that could be considered as competitors to GFC, although GFC is unique in the way that it will be the only game that allows users to log in with their own NFT assets and that is optimized for an exciting battle experience. The three main competitors are Brawl Stars, Thetan Arena and THE SANDBOX. Brawl Stars is not NFT-based and offers no P2E mechanics, which are foundational to building out a functional Metaverse. Thetan arena is a game that is built top-down, while GFC builds a Metaverse from the bottom-up to allow players to use their own NFTs, to which their social identity is linked to. This is why GFC starts from the existing NFT communities and builds a game for these communities instead of building a game first and then releasing NFTs in hope of people buying them. The core attraction to NFTs is the social status and identity it gives them. A successful Metaverse must allow players to keep these intangibles.\r\nThe Sandbox game is quite different to GFC&apos;s game genre, as it creates a Metaverse that is optimized for building and relaxing and is thus fundamentally not designed and optimized for PvP excitement.\r\n\r\nBy establishing partnerships with tier-1 NFT projects like CyberKongz, Illuvium, Animetas and many others, Galaxy Fight Club is taking advantage of network effects and is the go-to platform for cross-IP battles. GFC is developing a first-of-its-kind cross-chain token contract, which allows the game to run on an L2 for speed and lower transaction fees while optimizing trade volume on NFTs by keeping them on the Ethereum mainnet.\r\n\r\n \r\n## Investors \r\n### Galaxy Fight Club raised $7M led Animoca Brands, Sequoia and Skyvision Capital\r\n![Galaxy Fight Club Investors](https://res.cloudinary.com/polkastarter/image/upload/v1643964066/projects/galaxy-fight-club/gfc-investors.jpg)\r\n\r\nAdditional backers and partners in the round include The Chernin Group, YGG SEA, Spartan Capital, Sfermion, PKO Investments, Infinity Ventures Crypto, Huobi Innovation Labs, Kucoin Labs, Rarestone Capital, Formless Capital, Gerstenbrot Capital, Perion, Double Peak Group, Polkastarter, MEXC, MoonEdge, Edimus Capital, Bondly, Astronaut Capital, RR2 Capital, Panony, 18 Ventures, Zokyo as well as many other notable angel investors including Kevin Lin of Twitch, Kun Gao of Crunchyroll and Matt Finick of ROBLOX and Marvel Studios.\r\n\r\n## Team\r\n### Experienced team with extensive background in the gaming industry\r\n\r\nThe team has a background in the mobile gaming and NFT industry. They have a keen eye to discover opportunities for disruption and skilfully navigate the competitive landscape.\r\n\r\n* Ado: CEO with 4+ years of experience in strategy consulting and the mobile games industry. Advised Fortune 500 companies as an ex-MBBD consultant before starting Galaxy Fight Club.\r\n\r\n* Dan: Lead Game Designer with 5+ years of experience at OneSoft and VNG. Dan worked as the lead game designer previously on Samurai Shodown and was also part of the team that created Metal Wings, GunPow 3D, AU Mobi and other mobile titles.\r\n\r\n* Dave: CTO with 2+ years of experience in Blockchain tech and smart contract development. His Solidity coding expertise enables cool things like cross-chain contracts and on-chain provably-fair loot boxes.\r\n\r\n\r\n## Token Utility\r\n### $GCOIN: The in-game, deflationary token underpinning the game economy\r\n$GCOIN is designed to be the in-game currency that underpins the entire game economy and game loop. The token is designed to be deflationary even if no trading would occur on the market. This is because there is a fixed supply of 150M $GCOIN, while the demand is driven by in-game utilities, which will increase as the userbase of the game increases.\r\n![GCOIN Token Utility](https://res.cloudinary.com/polkastarter/image/upload/v1638227942/projects/galaxy-fight-club/gfc-token-utility.png)","legal_name":"","ethereum":false,"bsc":false,"polygon":true,"celo":false,"application_form_url":null,"kyc_form_url":"https://galaxyfightclub.synaps.me","published_at":"2021-11-25T20:15:33.189Z","applications_opened_at":"2021-11-30T01:19:53.605Z","applications_closed_at":"2021-12-14T22:13:24.292Z","kyc_opened_at":"2021-12-16T00:45:13.411Z","kyc_closed_at":"2022-01-04T15:53:14.287Z","submitted_to_kyc_at":"2021-12-16T00:45:04.303Z","submitted_to_lottery_at":null,"contracts_deployed_at":null,"created_at":"2021-11-25T08:04:54.207Z","updated_at":"2022-02-16T18:28:02.831Z","permitted_events":["open_kyc"],"permitted_states":["kyc_open"],"slug":"galaxy-fight-club","short_description":"The First Cross-IP PVP game for the NFT Universe","ido_ends_at":"2022-01-07T11:00:00.000Z","ido_distributed_at":"2022-01-07T11:30:00.000Z","prospect_usd_token_price":0.5,"prospect_initial_market_cap":1687500.0,"prospect_initial_token_circulation":3375000.0,"token_symbol":"GCOIN","ido_readiness_state":"ready","pool_contracts_available":true,"pool_contract_address":"0x34e5539123cd6589e6EC90Df85796F0c28e2a12D","pool_chain":"polygon","finalized":true,"finalized_today":false,"applications_open_at":null,"applications_close_at":null,"pool_slug":"galaxy-fight-club-polygon","pool_progress":100,"main_pool_id":311,"pool_token_to":"MATIC","pool_token_address":"0x071AC29d569a47EbfFB9e57517F855Cb577DCc4C","pool_is_live":false,"pool_is_upcoming":false,"hidden":false}'
