                                            className="d-none"
                                            id="projectJson53"
                                            data-project-json='{"id":53,"name":"impactMarket","state":"kyc_closed","ido_starts_at":"2021-12-22T13:00:00.000Z","min_allocation":null,"max_allocation":225.0,"total_raise":500000.0,"logo_url":"https://storage.googleapis.com/polkastarter-production-assets/nvjb7spcv93sc3tylqs9nn2ec4fs","card_background_url":"https://storage.googleapis.com/polkastarter-production-assets/4zfkkoc6uzegly5p731n0kvqkkat","cover_url":"https://storage.googleapis.com/polkastarter-production-assets/r5s88f4lpx9y3dcgtztqbfvt2odj","website_url":"https://www.impactmarket.com/","twitter_handle":"IPCTmarket","telegram_handle":"impactMarket","twitter_url":"https://twitter.com/IPCTmarket","telegram_url":"https://t.me/impactMarket","medium_url":"https://medium.com/impactmarket","linkedin_url":"https://www.linkedin.com/company/impactmarket","discord_url":"https://discord.gg/V6JWbM4ZQ3","github_url":"https://github.com/impactMarket","founded_at":"2021-12-09","whitepaper_url":"","description":"## Highlights \r\n* impactMarket is a poverty alleviation protocol that enables any vulnerable community to implement financial inclusive mechanisms, like Unconditional Basic Income (UBI).\r\n* After 1 year, the protocol distributed $1.5M+ cUSD as UBI to almost 30 thousand beneficiaries living in the most vulnerable communities in 25+ developing countries. \r\n* impactMarket has raised $1.6M+ from mission-aligned investors, including Celo Foundation, Davoa Capital, Flori Ventures, Eutopia Foundation, and more.\r\n\r\n## Product \r\n### Decentralized Poverty Alleviation Protocol\r\nimpactMarket is an application-layer protocol designed to fight extreme poverty quickly and effectively, while empowering local \u0026 social change, bottom-up economic development, and prosperity.\r\n\r\nIt operates as a DAO, on top of the Celo network, uses cUSD (Celo Dollar) as the main digital currency and $PACT as its governance token, while running autonomously through smart contracts.\r\n\r\nBeyond UBI, impactMarket is working on providing free access to more financial services. Given the Celo network design and its current DeFi ecosystem, impactMarket is uniquely positioned to drive DeFi adoption and retention by integrating \u0026 offering DeFi solutions directly on mobile to those who need them the most. Hence, impactMarket will emphasize but will not be limited to:\r\n\r\n* Deposit assets and redirect the earned interest (or yield) to support vulnerable communities while claiming $PACT rewards. No donation? No problem. \r\n* Micro Credit: Beneficiaries will be able to use their own $PACT or $CELO as collateral for instant access to credit in cUSD.\r\n* Virtual Fair, where beneficiaries can sell and buy stuff (or content as NFT) among their communities, like community-based e-commerce marketplaces.\r\n* Collective Savings + prize, incentivizing beneficiaries to save together within their own communities. The higher the savings, the higher the prizes.\r\n* Crowd Lending where anyone can allocate assets as collateral and delegate credit in $cUSD to specific beneficiaries, causes, or communities.\r\n* Explore other possibilities, such as beneficiaries’ stories \u0026 art as NFTs (could be used as collateral, or to help raise funds for their community), access to yield farming, learn \u0026 earn, paid tasks and more.\r\n\r\nIn order for impactMarket to monitor and provide transparency and analytics about its impact on its stakeholders, the team is developing an impact measurement framework, made available via different aggregated sources, including on-chain data analysis performed on the Celo network. \r\n\r\n* Self-reporting from beneficiaries through regular specific surveys that measure life quality improvement and progress on several verticals of their life, including health, education, impact on their children, security, happiness, nutrition, etc.\r\n* Identify merchants, key intermediaries, and influencers. Attach content to transactions.\r\n* On-chain data analysis to understand, on a deeper level, economic development, activity, growth, and behavior. Cross public information and benchmark different approaches.\r\n* Predictive analytics to forecast potential outcomes when kickstarting new communities.\r\n![impactMarket Product](https://res.cloudinary.com/polkastarter/image/upload/v1639049413/projects/impactmarket/impact-product.jpg)\r\n\r\n## Problem\r\n### Extreme Poverty: the biggest limiting factor for human potential and development\r\nAbout 10% of the population in the world lives in extreme poverty. At the same time, decades of progress tackling this problem are now in reverse due to covid.\r\n\r\n* ~2 billion people unbanked;\r\n* ~30% of aid is lost to corruption;\r\n* Too many intermediaries \u0026 borders impacting philanthropic efforts;\r\n* Due to covid, extreme poverty can rise to 750 million people this year.\r\n\r\nGlobally, the number of people living in extreme poverty declined from 36% in 1990 to 10 % in 2015. But the pace of change is decelerating and the COVID-19 crisis risks reversing decades of progress in the fight against poverty. New research published by the UNU World Institute for Development Economics Research warns that the economic fallout from the global pandemic could increase global poverty by as much as half a billion people, or 8% of the total human population. This would be the first time that poverty has increased globally in thirty years, since 1990.\r\n\r\nMore than 700 million people, or 10%of the world population, still live in extreme poverty today, struggling to fulfill the most basic needs like health, education, and access to water and sanitation, to name a few. The majority of people living on less than $1.90 a day live in sub-Saharan Africa. Worldwide, the poverty rate in rural areas is 17.2% — more than three times higher than in urban areas.\r\n\r\n\r\n## Solution \r\n### Fighting poverty through web3-powered financial inclusion\r\nimpactMarket is an open, free, censorship-resistant, and borderless impact-driven crowdfinance marketplace to fight poverty. \r\n\r\nIt merges the principles of unconditional basic income with blockchain technology to enable any community to have access to a basic income through its own UBI smart contract with specific parameters based on their reality. Beneficiaries added to those contracts can claim cUSD on a regular basis and use it for whatever they want or need.\r\n\r\nimpactMarket has been a catalyst to unlock human potential by effectively fighting poverty, with the ambition to reach 1B+ people by 2030 and become a significant contributor to achieve Sustainable Development Goal 1. On the way there, it might also become one of the biggest open financial infrastructures. impactMarket is being built to:\r\n\r\n* Accelerate poverty alleviation, access to finance, and wealth redistribution\r\n* Ensure the primacy of the individual and the social objective over capital\r\n* Allow democratic governance, autonomous management, and independence of the protocol from public and central authorities\r\n\r\n#### “Basic income is venture capital for the people” - Rutger Bregman\r\n\r\nAligned with the protocol’s mission and vision, it’s been developed to be very scalable and self-sustaining. Currently scoped, the core deliverables will be:\r\n\r\n* Integration with existing DeFi protocols on Celo to provide open, free \u0026 easy access to Mobile Finance (MoFi), focused on beneficiaries.\r\n* Self-governed communities through social consensus, where beneficiaries vouch for who they believe should/need access to a basic income while building reputation, identity, \u0026 trust indicators.\r\n* Develop a low volatility currency, not pegged to anything, minted by beneficiaries, and backed by a reserve of different assets deposited by contributors (instead of donations) to help absorb volatility. This can unlock orders of magnitude improvements in scalability and reachability.\r\n\r\n\r\n## Business Model\r\n### Let’s DAO it! Impact-driven DeFi solutions\r\nimpactMarket is an open and free protocol that anyone can access and benefit from. As the protocol is now becoming a DAO, it will be up to token holders to propose and decide new mechanisms and revenue streams.\r\n\r\nCurrent potential revenue streams being considered to help the company to support operations, development and growth:\r\n* SaaS with advanced dashboards, impact measurement, and analytics for major contributors and decision-makers.\r\n* Licensing through whitelabel to be used by larger organizations, cities, or even countries.\r\n\r\nWith $2M+ raised (pre-seed + seed equity rounds + IDO), impactMarket will be able to keep developing its technology and expanding to more vulnerable communities for the next 18+ months.\r\n\r\n\r\n## Market \r\n### Extreme poverty and financial assistance: for an old problem, new solutions\r\nThere’s still a massive lack of access to finance, and a lack of efficiency and transparency on philanthropy, especially when it comes to the distribution of the funds that are raised, because there is no infrastructure and there are too many intermediaries and borders to reach those most in need. There are some projects related with cash transfers to people living in vulnerable conditions (but not using blockchain, such as GiveDirectly), and there are several web3 projects focused on UBI, such as Proof of humanity, GoodDollar, CirclesUBI, Anatha and WorldCoin. impactMarket stands out as a decentralized protocol focused on poverty alleviation, enabling vulnerable communities from all over the world to benefit from Unconditional Basic Income. Thanks to impactMarket’s smart contracts and DAO mechanisms, it can scale fast, thus reaching more beneficiaries where it’s most needed, distributing UBI with efficiency and transparency, while empowering users - PACT token holders - to influence the future of the protocol. \r\n\r\n \r\n## Investors  \r\n### Mission-aligned investors\r\nThe project has raised more than $1.6M since inception from VCs and individuals.\r\n* Institutional investors: Celo Foundation, Davoa Capital, Flori Ventures, Eutopia Foundation, \u0026 Valora;\r\n* Individual investors include Diogo Monica (Anchorage), Marek Olszewski (Celo), Deepak Nuli (Kresko), Evan Kereiakes (Ubeswap) and more.\r\n![impactMarket Investors](https://res.cloudinary.com/polkastarter/image/upload/v1639049470/projects/impactmarket/impactmarket-investors.jpg)\r\n\r\n\r\n## Team \r\n### Impact (Dream) Team\r\n\r\nThe impactMarket team has a strong background on philanthropy, web3 and social impact, with experience on working in organizations like Uphold, eSolidar, Microsoft, WEF, Facebook, Unicef, Human Rights Watch and WHO.\r\n\r\n* Marco Barbosa (Co-founder \u0026 CEO): Forbes 30 under 30 for Social Entrepreneurship. Co-founder (2013) eSolidar. Master’s Degree in Computer Science.\r\n* Bernardo Vieira (Co-founder \u0026 CTO): Blockchain developer. Won multiple blockchain hackathons. Bachelor in Computer Science.\r\n* Afonso Barbosa (Co-founder \u0026 CPO): Worked at Facebook, Quanta, Vyu Labs, eSolidar and Edigma/Displax. Degree in Mathematics and Computer Science.\r\n\r\n![impactMarket Team](https://res.cloudinary.com/polkastarter/image/upload/v1639127989/projects/impactmarket/impactmarkte-team.jpg)\r\n\r\n## Token Utility \r\n### $PACT: governing the protocol’s direction and impact\r\n\r\n$PACT tokens will act as a global medium of governance that empowers participants through decision-making and comprehensive incentive mechanisms. Token holders will be granted the ability to influence the future health, direction, growth, and impact of the protocol.\r\n\r\n* **Voting power delegation** Any token holder can delegate voting power to themselves, someone they trust, or key participants of the protocol. Those with the most delegated voting power can be considered impactMarket ambassadors.\r\n* **Communities’ verification and validation** Each new UBI community will be submitted as a proposal and reviewed by token holders, who can vote for its approval. Verified communities are able to request funding directly from the DAO.\r\n* **Manage/Update key parameters, calculation formulas, treasury allocation, and incentives**\r\n\r\nAbout 40% of total supply will be available to be claimed by participants through impact farming, an incentive mechanism that allows anyone to claim $PACT tokens as rewards for their cUSD contributions/donations to vulnerable communities.\r\n","legal_name":"","ethereum":false,"bsc":false,"polygon":false,"celo":true,"application_form_url":null,"kyc_form_url":"http://impactmarket.synaps.me","published_at":"2021-12-09T12:06:11.136Z","applications_opened_at":"2021-12-09T20:14:38.393Z","applications_closed_at":"2021-12-17T16:42:14.551Z","kyc_opened_at":"2021-12-18T15:57:31.404Z","kyc_closed_at":"2021-12-21T19:06:40.655Z","submitted_to_kyc_at":"2021-12-18T15:57:25.512Z","submitted_to_lottery_at":null,"contracts_deployed_at":null,"created_at":"2021-12-09T10:56:58.808Z","updated_at":"2021-12-22T13:23:15.651Z","permitted_events":["open_kyc"],"permitted_states":["kyc_open"],"slug":"impactmarket","short_description":"Decentralized Poverty Alleviation Protocol","ido_ends_at":"2021-12-22T13:33:00.000Z","ido_distributed_at":"2021-12-22T15:00:00.000Z","prospect_usd_token_price":0.003333,"prospect_initial_market_cap":null,"prospect_initial_token_circulation":null,"token_symbol":"PACT","ido_readiness_state":"ready","pool_contracts_available":true,"pool_contract_address":"0xBbA4ED9462ABDb4010Bb12881895fb1E77284B6b","pool_chain":"celo","finalized":true,"finalized_today":false,"applications_open_at":null,"applications_close_at":"2021-12-17T16:00:00.000Z","pool_slug":"impact-market-celo","pool_progress":100,"main_pool_id":310,"pool_token_to":"CELO","pool_token_address":"0x46c9757C5497c5B1f2eb73aE79b6B67D119B0B58","pool_is_live":false,"pool_is_upcoming":false,"hidden":false}'
