                                            data-project-json='{"id":47,"name":"Unbound Finance","state":"kyc_closed","ido_starts_at":"2021-12-13T16:00:00.000Z","min_allocation":null,"max_allocation":280.0,"total_raise":200000.0,"logo_url":"https://storage.googleapis.com/polkastarter-production-assets/i61wvg44l06hvxlu40k34o0spgyj","card_background_url":"https://storage.googleapis.com/polkastarter-production-assets/fcyx39gr9p0yw418wwco7wulwynp","cover_url":"https://storage.googleapis.com/polkastarter-production-assets/1cr3y44jf1jvsdctjcy54yxdpzek","website_url":"https://www.unbound.finance/","twitter_handle":"@unboundfinance","telegram_handle":"unboundfinance","twitter_url":"https://twitter.com/@unboundfinance","telegram_url":"https://t.me/unboundfinance","medium_url":"https://unboundfinance.medium.com/","linkedin_url":"","discord_url":"","github_url":"","founded_at":"2021-11-30","whitepaper_url":"","description":"## Highlights \r\n* Unbound Finance is a decentralized, non-custodial platform that is building a derivative layer on top of existing AMMs to increase the overall capital efficiency in the DeFi space.\r\n* Liquidity providers can compound their earnings by leveraging their LP tokens to mint $UND, a decentralized, cross-chain stablecoin, soft-pegged to the US dollar.\r\n* Unbound Finance has raised $8 million from top-tier VCs such as Pantera, Hashed, CMS, Arrington XRP, Zee Prime and many others.\r\n\r\n## Product \r\n### Interest-free crypto loans without liquidation risk \r\nUnbound Finance is a decentralized lending platform that issues interest-free crypto loans to its users against liquidity provisioning tokens (LPTs) as collateral. The loans are given out in the form of synthetic assets, including $uETH and Unbound&apos;s native stablecoin $UND. Unbound loans are perpetual, meaning there is no deadline on loan repayment and users are free from the risk of collateral liquidation. \r\n\r\nUsers can further grow their existing DeFi profits by using the borrowed funds to trade on DEXs, provide liquidity to $UND pools or participate in staking and yield farming activities to earn additional rewards. Unbound Finance is built as an aggregator layer over the existing AMMs in the DeFi ecosystem and intends to unlock the liquidity trapped in various DEXs across multiple, balkanized blockchains.  \r\n![Unbound Protocols](https://res.cloudinary.com/polkastarter/image/upload/v1638890172/projects/unbound/unbound-protocol.jpg)\r\n\r\n\r\n## Problem\r\n### Large amounts of idle liquidity in DeFi\r\nBillions of dollars of liquidity are lying idle across decentralized exchanges. Unbound Finance is targeting to make this “illiquid” liquidity more usable. For example in Uniswap, more than $1B liquidity is lying idle in stablecoin pairs only. Through Unbound, this liquidity can be made movable and the liquidity can be taken to various DEXs across different chains. When LPTs of Uniswap are deposited at Unbound as collateral, the minted $UND can then be cross-bridged on BSC to trade various assets on the DEXs of Binance Smart Chain.\r\n\r\n\r\n## Solution \r\n### Unlocking trapped liquidity through interest-free loans\r\nBuilt as an aggregator layer over the existing AMMs, Unbound aims to unlock the liquidity trapped in DeFi DEXs and to enable the flow of this liquidity from one chain to another without physically removing it. Trapped liquidity is a problem that affects the capital efficiency in DeFi. Unbound solves this problem by using “trapped” crypto assets (like LPTs) as collateral, which otherwise would find no further use case. Unbound offers its users newer and better opportunities of yield by unlocking and enabling the flow of liquidity across multi-chain DEXs. Unbound&apos;s decentralized, cross-chain stablecoin $UND is pivotal in promoting interoperability and profitability across various balkanized blockchains.\r\nUnbound Finance is a liquidity-backed lending protocol that is targeted towards increasing the overall capital efficiency of the DeFi ecosystem. To solve this problem, Unbound enables users to leverage their otherwise idle crypto assets (LPTs) to borrow stablecoin loans and further use the borrowed funds to compound their yield by earning over and above their existing DeFi profits.\r\n\r\nNext, Unbound aims to solve the problem of collateral liquidation seen with most DeFi lending platforms by using LPTs that are a lot more stable than a single crypto asset used as collateral. Additionally, Unbound maintains a SAFU (Secure Asset Fund for Users), an emergency reserve for safeguarding user assets in case of black-swan events.\r\n\r\nFinally, Unbound intends to open newer and better avenues of yield by creating the first-ever truly decentralized, cross-chain stablecoin $UND that will be instrumental in promoting inter-blockchain operability and profitability. \r\n![Unbound Chains](https://res.cloudinary.com/polkastarter/image/upload/v1638890383/projects/unbound/unbound-chains.jpg)\r\n\r\n## Business Model\r\n### Sustainable revenue through stablecoin minting fees\r\n Unbound charges a 0.6% minting fee whenever someone locks up their liquidity pool tokens and mints the stablecoin $UND. 20% of this fee (12bps) goes to Unbound’s treasury. Unbound is targeting the $40 billion AMM market, which will be at a $300 billion valuation in the next three years. If it can capture 5% of this liquidity in 3 years, $15 billion from AMMs across multiple chains can be locked up in Unbound. At an average 50% loan-to-value, that would translate to $7.5 billion in $UND issued and generate fees totaling $45 million. Of this $45 million, $9 million would be parked with Unbound. This is considering a single transaction. Considering an average of 10 transactions by users (locking and unlocking) over three years, revenues would go up to $90 million.\r\n\r\n## Market \r\n### A new player in the liquidity-backed collateralized loan market \r\nUnbound is an upcoming blockchain startup designed to increase the overall efficiency of the DeFi ecosystem by providing liquidity-backed collateralized loans to crypto users. As such, it is competing with a number of competitors including Maker DAO, Compound, Synthetix and Nexo. The below table compares Unbound with its competitors: \r\n![Unbound Market](https://res.cloudinary.com/polkastarter/image/upload/v1638296673/projects/unbound/unbound-market.jpg)\r\n\r\n\r\n## Investors  \r\n### $8M raised from top VCs and angel investors\r\nUnbound has raised $8 million in the pre-public rounds from notable investors. It has received investments from founders of Angelist, Enjin, Gnosis, Kyber, Swissborg, Daomaker, Polkastarter, Fantom, Harmony, WikiHow, Zilliqa, Tomochain, Polygon, Coin 98 Analytics, Matrix Partners, Frontier, Curve, Synaps and many others. VCs such as Pantera Capital, Arrington XRP Capital, Fintech, Hashed, CMS, Ark Stream, LD Capital, Zee Prime, Ledger Prime, Kucoin, Gate, MEXC and many others have supported Unbound Finance as well.\r\n![Unbound VCs](https://res.cloudinary.com/polkastarter/image/upload/v1638296725/projects/unbound/unbound-vcs.jpg)\r\n![Unbound investors](https://res.cloudinary.com/polkastarter/image/upload/v1638296723/projects/unbound/unbound-investors.jpg) \r\n\r\n## Team \r\n### World-class team with exceptional executives\r\n \r\n**Founder \u0026 CEO - Tarun Jaswani:** Tarun is a developer, technologist and entrepreneur. He has experience of 21 years in Digital Ad Networks, VR and Blockchain. He started building Unbound Finance in March 2020 when only 200 million were locked in 3-4 AMMs in the space. Once he understood the importance of AMMs and the new iteration in value discovery, it was all about building a derivative layer that could make the space capital efficient. Unbound was one of the few projects that had proof-of-concept before the whitepaper or the domain name was registered and each investor was brought on after the working product demo. \r\n\r\n**CMO - Pratik Oswal:** Pratik Oswal is the founder of IQube. He is ranked amongst the top 4% of the highest IQ in the world by Mensa. He has authored 7 books on Mathematics, which are currently being used by over 100,000 students in over 300 schools across India setting the first syllabus in India on IQ. He is an Engineering gold medallist in the field of Electronics and telecommunications and is currently pursuing a Master’s in Economics. He has been actively studying blockchain since 2016 and has worked closely with several projects in the space. He has a passion for fundamental and technical analysis and was heading the technical analysis section of AltcoinBuzz, a leading crypto-centric media house. He has written over 220 articles for Altcoin Buzz on fundamental and technical Analysis of over 100 projects and is popularly known in the crypto world as cryptomathemagician.\r\n\r\n\r\n## Token Utility\r\n### $UNB: Unbounded Finance’s native governance token \r\nIn the Unbounded Finance ecosystem, two tokens are at its core: $UND and $UNB. $UND is an ERC-20 stablecoin soft-pegged to the US dollar, whereas $UNB is the protocol’s governance token. Holders of $UNB will be active members of the DAO and will be able to vote on all protocol changes, whitelist pools for $UND minting and set the loan-to-value parameters. $UNB will be live on multiple chains like Ethereum, Polygon, Binance Smart Chain and Harmony and later on Solana, KCC, HECO, OKEx chain and Klaytn. It will also be listed on multiple DEXs like Uniswap, Balancer, PancakeSwap, Quickswap, Balancer, Curve, DFYN, DMM and more.\r\n\r\n","legal_name":"","ethereum":false,"bsc":true,"polygon":false,"celo":false,"application_form_url":null,"kyc_form_url":"http://unbound-finance.synaps.me","published_at":"2021-12-07T16:50:02.616Z","applications_opened_at":"2021-12-07T19:34:24.825Z","applications_closed_at":"2021-12-10T13:06:52.384Z","kyc_opened_at":"2021-12-11T19:34:45.186Z","kyc_closed_at":"2022-01-05T18:10:57.442Z","submitted_to_kyc_at":"2021-12-11T19:34:13.793Z","submitted_to_lottery_at":null,"contracts_deployed_at":null,"created_at":"2021-11-30T17:52:19.339Z","updated_at":"2022-01-05T18:11:02.928Z","permitted_events":["open_kyc"],"permitted_states":["kyc_open"],"slug":"unbound-finance","short_description":"Cross-Chain Aggregator Layer for AMMs","ido_ends_at":"2021-12-13T18:00:00.000Z","ido_distributed_at":"2021-12-14T15:00:00.000Z","prospect_usd_token_price":0.012,"prospect_initial_market_cap":null,"prospect_initial_token_circulation":null,"token_symbol":"UNB","ido_readiness_state":"ready","pool_contracts_available":true,"pool_contract_address":"0x06a0bD92daCc8FC3E49EDD87d5201F47d6c2c02F","pool_chain":"binance","finalized":true,"finalized_today":false,"applications_open_at":"2021-12-07T13:00:00.000Z","applications_close_at":"2021-12-10T13:00:00.000Z","pool_slug":"unbound-finance-binance","pool_progress":100,"main_pool_id":303,"pool_token_to":"BNB","pool_token_address":"0x301AF3Eff0c904Dc5DdD06FAa808f653474F7FcC","pool_is_live":false,"pool_is_upcoming":false,"hidden":false}'
